<template>
  <div class="size">
    <keep-alive>
      <router-view style="padding-bottom: 50px;" />
    </keep-alive>
    <!-- 客户端标签 -->
    <van-tabbar v-if="user === 'customer'" route active-color="#F4A61C" inactve-color="#959595">
      <van-tabbar-item
        v-for="(item, inx) of navList"
        :key="inx"
        :name="item.route"
        :to="item.route"
      >
        <img slot="icon" :src="getActiveNav === item.route ? item.activeIcon : item.icon" />
        {{item.title}}
      </van-tabbar-item>
    </van-tabbar>

    <!-- 牧民端标签 -->
    <van-tabbar v-if="user === 'nomads'" route active-color="#F4A61C" inactve-color="#959595">
      <van-tabbar-item
        v-for="(item, inx) of nomadsList"
        :key="inx"
        :name="item.route"
        :to="item.route"
      >
        <img slot="icon" :src="getActiveNav === item.route ? item.activeIcon : item.icon" />
        {{item.title}}
      </van-tabbar-item>
    </van-tabbar>

    <!-- 屠宰端标签 -->
    <van-tabbar v-if="user === 'slaughter'" route active-color="#F4A61C" inactve-color="#959595">
      <van-tabbar-item
        v-for="(item, inx) of slaughterList"
        :key="inx"
        :name="item.route"
        :to="item.route"
      >
        <img slot="icon" :src="getActiveNav === item.route ? item.activeIcon : item.icon" />
        {{item.title}}
      </van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
// import {copyObj} from 'cai-tools/utils'

export default {
  name: "index",
  data() {
    return {
      user: "nomads",
      navList: [
        {
          title: "首页",
          icon: require("../assets/images/tabs/home.png"),
          activeIcon: require("../assets/images/tabs/act-home.png"),
          route: "/home"
        },

        {
          title: "牦牛认养",
          icon: require("../assets/images/tabs/yak.png"),
          activeIcon: require("../assets/images/tabs/act-yak.png"),
          route: "/yak-adopt"
        },
        {
          title: "帮扶",
          icon: require("../assets/images/tabs/help.png"),
          activeIcon: require("../assets/images/tabs/act-help.png"),
          route: "/loving-help"
        },
        {
          title: "我的",
          icon: require("../assets/images/tabs/personal.png"),
          activeIcon: require("../assets/images/tabs/act-personal.png"),
          route: "/personal"
        }
      ],
      nomadsList: [
        {
          title: "牧民端首页",
          icon: require("../assets/images/tabs/home.png"),
          activeIcon: require("../assets/images/tabs/act-home.png"),
          route: "/nomads-home"
        },
        {
          title: "消息",
          icon: require("../assets/images/tabs/message.png"),
          activeIcon: require("../assets/images/tabs/act-message.png"),
          route: "/nomads-message"
        },
        {
          title: "我的",
          icon: require("../assets/images/tabs/personal.png"),
          activeIcon: require("../assets/images/tabs/act-personal.png"),
          route: "/nomads-my"
        }
      ],
      slaughterList: [
        {
          title: "屠宰端首页",
          icon: require("../assets/images/tabs/home.png"),
          activeIcon: require("../assets/images/tabs/act-home.png"),
          route: "/slaughter-home"
        },
        {
          title: "消息",
          icon: require("../assets/images/tabs/message.png"),
          activeIcon: require("../assets/images/tabs/act-message.png"),
          route: "/slaughter-message"
        },
        {
          title: "我的",
          icon: require("../assets/images/tabs/personal.png"),
          activeIcon: require("../assets/images/tabs/act-personal.png"),
          route: "/slaughter-my"
        }
      ]
    };
  },
  computed: {
    /**
     * @description: 获取活跃的导航栏
     * @date 2020/2/11
     */
    getActiveNav() {
      // return copyObj(this.$route.path)
      // console.log(this.$route.path);
      return this.$route.path;
    }
  },
  created(){
    let type=localStorage.getItem('type');
    if(type==1){
      this.user="customer"
    }else if(type==2){
      this.user="nomads"
    }else if(type==3){
      this.user="slaughter"
    }
  }
};
</script>

<style scoped>
</style>
